import React from "react";
import Fade from "react-reveal/Fade";
import "./mystyles.scss";

const GameModal = props => {
  return (
    <div className="modal is-active">
      <div className="modal-background" />
      <div className="modal-content" style={{ width: "auto", height: "auto" }}>
        {props.children}
      </div>
      <button
        className="modal-close is-large"
        ariaLabel="close"
        onClick={props.onClose}
      />
    </div>
  );
};

class App extends React.Component {
  state = {
    modal: null,
    imageIsReady: false
  };

  componentDidMount() {
    const img = new Image();
    img.src = "img/my_pic.jpg";
    img.onload = () => {
      this.setState({ imageIsReady: true });
    };
  }

  closeModal() {
    this.setState({ modal: null });
  }

  modalRtsrpg() {
    return (
      <GameModal onClose={() => this.closeModal()}>
        <iframe
          title="game"
          src="games/rtsrpg"
          style={{
            width: 800,
            height: 500
          }}
        />
        <div className="box content">
          <h3>Controls:</h3>
          <p>WASD: Movements</p>
          <p>Left Click: Select / Move to</p>
          <p>F1: Select the Ranger</p>
          <p>F2: Select the Warrior</p>
          <p>Space: Pause</p>
          <p>U: Mute / Unmute</p>
        </div>
      </GameModal>
    );
  }

  renderModal() {
    switch (this.state.modal) {
      case "rtsrpg":
        return this.modalRtsrpg();
      default:
        return null;
    }
  }

  renderInfo() {
    return (
      <section className="hero is-primary is-fullheight">
        <div className="hero-body">
          <div className="container has-text-centered">
            <Fade>
              <figure
                className="image"
                style={{ maxWidth: 512, margin: "0 auto" }}
              >
                <img
                  alt="Yonsh Lin"
                  src="img/my_pic.jpg"
                  className="is-rounded"
                />
              </figure>
            </Fade>
            <Fade left delay={200} distance="0.6em">
              <h2 className="subtitle is-size-4" style={{ marginTop: 20 }}>
                Hi, I'm
              </h2>
            </Fade>
            <Fade left delay={400} distance="0.6em">
              <h1 className="title is-size-1">Yonsh Lin</h1>
            </Fade>
          </div>
        </div>
      </section>
    );
  }

  renderProjects() {
    return (
      <section className="hero is-warning">
        <div className="hero-body">
          <div className="container">
            <Fade bottom distance="1em" duration={500}>
              <h1 className="title has-text-centered">
                Take a look at some of my indie game projects
              </h1>
            </Fade>
            <div className="columns is-multiline">
              <Fade bottom cascade distance="1em">
                <div className="column is-half is-one-third-widescreen">
                  <div className="card">
                    <div className="card-image">
                      <figure className="image">
                        <img src="img/rtsrpg.jpg" alt="Placeholder" />
                      </figure>
                    </div>
                    <div className="card-content">
                      <div className="content">
                        <p className="title">Defenders of the Realm</p>
                        <p className="subtitle has-text-grey">RTS/RPG</p>
                        <p>
                          A game I made when{" "}
                          <a
                            href="https://esotericsoftware.com"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            Spine
                          </a>{" "}
                          just released.
                        </p>
                        <p style={{ marginBottom: 0 }}>Features:</p>
                        <ul style={{ marginTop: 0 }}>
                          <li>Threat system</li>
                          <li>Can issue commands when paused</li>
                          <li>
                            The warrior has an AI that protects the ranger
                          </li>
                          <li>Spine animation</li>
                        </ul>
                        <button
                          className="button is-primary"
                          onClick={() => this.setState({ modal: "rtsrpg" })}
                        >
                          Try it out!
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="column is-half is-one-third-widescreen">
                  <div className="card">
                    <div className="card-image">
                      <figure className="image">
                        <img
                          src="https://bulma.io/images/placeholders/1280x960.png"
                          alt="Placeholder"
                        />
                      </figure>
                    </div>
                    <div className="card-content"></div>
                  </div>
                </div>
              </Fade>
            </div>
          </div>
        </div>
      </section>
    );
  }

  render() {
    if (this.state.imageIsReady) {
      return (
        <div className="App">
          {this.renderInfo()}
          {this.renderProjects()}
          {this.renderModal()}
        </div>
      );
    }

    return null;
  }
}

export default App;
